.square {
    background: rgba(255, 255, 255, 0.363);
    border: 1px solid rgb(255, 255, 255);
    float: left;
    font-size: 54px;
    font-weight: bold;
    line-height: 64px;
    height: 64px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 64px;
  }
  
  .square:focus {
    outline: none;
  }
